.price-item {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 360px;
  padding: 58px 20px 60px;
  border: 7px solid $color-default-white;
  background-color: $color-default-white;
  z-index: 5;

  &:hover,
  &:focus {
    padding: 58px 20px;
    border-image: $gradient-border-tickets;
    border-image-slice: 1;

    & .btn--price:not([disabled]) {
      color: $color-default-white;
      background: $gradient-button;
      outline-color: transparent;
    }

    .price-item__price {
      margin-bottom: 34px;
    }

    .price-item__button {
      max-width: 205px;
      padding-top: 27px;
      padding-bottom: 27px;
    }
  }

  @include vp-1365 {
    width: 440px;
  }
}

.price-item__title {
  margin: 0 0 4px 0;
  font-size: 26px;
  font-weight: 700;
  line-height: 1.2;
}

.price-item__description {
  margin: 0 0 69px 0;
  font-weight: 300;
}

.price-item__price {
  position: relative;
  margin: 0 0 36px 0;
  font-size: 60px;
  font-weight: 700;
  line-height: 1.5;
  color: $color-egyptian-blue;
  z-index: 1;
  letter-spacing: 0.5px;

  &::before {
    position: absolute;
    content: attr(data-price);
    top: 20px;
    left: -19px;
    color: $color-whisper;
    opacity: 0.48;
    font-size: 100px;
    z-index: -1;
    transform: translateY(-50%);
    letter-spacing: -0.3px;
  }
}

.price-item__description span {
  position: relative;
  top: 0;
  right: 4px;
  color: $color-sapphire;
}

.price-item__button {
  max-width: 201px;
}
